<template>
    <div class="systemcon">
        <div class="header">
            <yd-navbar :title="title" bgcolor="#2c94ed" fontsize=".36rem" color="#ffffff" height=".9rem">
                <div slot="left" @click="hidesys()">
                    <yd-navbar-back-icon color="#ffffff"></yd-navbar-back-icon>
                </div>
            </yd-navbar>
        </div>
        <div class="systemcontent"  :content="content">
          <h1>{{content.MessageTitle}}</h1>
          <p class="ttime">{{content.CreateTime}}</p>
          <div class="content" v-html="content.MessageDetail"></div>
        </div>
    </div>
  
</template>

<script>
export default {
  data () {
    return {
      title:this.$t('header.Message'),

    }
  },
  props:['content'],
  created(){
    
  },
  methods:{
    hidesys(){
      this.$emit('refchild')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.systemcon{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999999;
  background: #fff;
  height: 100%;
  overflow-y: auto;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.systemcontent{
  padding: .3rem
}
.systemcontent h1{
  font-size: .3rem;
  color: #333;
  padding-bottom: .3rem;
}
.ttime{
  font-size: .26rem;
  color: #666;
}
.content{
  font-size: .28rem;
  color: #444;
  line-height: 4.5;
  text-align: justify;
}
</style>



